import { Link } from "gatsby"
import PropTypes from "prop-types"
import React from "react"

import { string } from "prop-types"
import styled from "styled-components"
import Container from "../components/Container"
import TopNav from "../components/TopNav"

import constants from "../utils/constants"
import logo from "../assets/images/logo.png"
import fb from "../assets/images/facebook-f.svg"
import twitter from "../assets/images/twitter.svg"
import instagram from "../assets/images/instagram.svg"
import newsletter from "../assets/images/envelope.svg"

import "../styles/global.css"
import "../styles/bootstrap.min.css"

const StyledSocialIcon = styled.div`
  height: 21px;
  width: 22px;
  margin-right: 14px;
  padding: 5px;
  background-image: url(${props => props.img});
  background-repeat: no-repeat;
  display: inline-block;
  opacity: 0.5;
  &:hover {
    cursor: pointer;
    opacity: 0.8;
  }
`
class Header extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      menuActive: false,
    }
  }

  toggleMenu = menuActive => {
    this.setState(prevState => ({
      menuActive: !prevState.menuActive,
    }))
  }

  render() {
    return (
      <header>
        <div
          className="outer"
          css={{
            background: constants.porcine,
          }}
        >
          <Container
            style={{
              padding: "0px",
            }}
          >
            <div
              className="headStrip1 d-none d-lg-block"
              style={{
                background: "white",
                fontFamily: `"Source Sans Pro",
                Helvetica,
                Arial,
                serif`,
                fontWeight: "400",
                fontSize: "14px",
                padding: "10px 10px 10px 20px",
              }}
            >
              <a href="https://www.facebook.com/WYEP91.3fm/">
                <StyledSocialIcon img={fb} />
              </a>
              <a href="https://twitter.com/WYEP">
                <StyledSocialIcon img={twitter} />
              </a>
              <a href="https://twitter.com/gatsbyjs">
                <StyledSocialIcon img={instagram} />
              </a>
              <a href="https://twitter.com/gatsbyjs">
                <StyledSocialIcon img={newsletter} />
              </a>

              <div className="f-right play-prog">
                <a href="http://wyep.org/program">PROGRAM SCHEDULE</a>
              </div>
              <div className="f-right play-prog">
                <a href="http://wyep.org/playlist">PLAYLIST</a>
              </div>
              <div className="slogan">WHERE THE MUSIC MATTERS</div>
            </div>
            <div
              className="headRow2"
              css={{
                background: constants.porcine,
                height: "100px",
                fontFamily: `"Source Sans Pro",
                Helvetica,
                Arial,
                serif`,
                padding: "20px 0px 20px 0px",
                width: "100%",
                display: "inline",
              }}
            >
              <TopNav active={this.state.menuActive} />
            </div>
            <div
              className="headRow3"
              css={{
                background: constants.red,
                height: "18px",
                color: "#fff",
                fontFamily: `"Source Sans Pro",
                Helvetica,
                Arial,
                serif`,
                textTransform: "capitalize",
                padding: "5px 25px 25px 10px",
                marginBottom: "20px",
                width: "100%",
              }}
            >
              Now Playing
            </div>
          </Container>
        </div>
      </header>
    )
  }
}
Header.propTypes = {
  siteTitle: PropTypes.string,
  headerText: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
  headerText: ``,
}

export default Header
